function InterestPeriodCreationDialog(props) {
	
  const [openInterestPeriodCreationDialog, setOpenInterestPeriodCreationDialog] = React.useState(false);

  const handleClickOpenInterestPeriodCreationDialog = () => {
    setOpenInterestPeriodCreationDialog(true);
  };

  const handleCloseInterestPeriodCreationDialog = (populateChanges) => {
    setOpenInterestPeriodCreationDialog(false);
	
	if(populateChanges) {
		postNewInterestPeriod();
	}
	
	setTimeout(function() {
		setBaseRate(0.0);
		setCommissionRate(0.0);
		setPeriodDate(new Date());
	}, 100);
  };
  
  const [baseRate, setBaseRate] = React.useState(0.0);
  
  const handleChangeBaseRate = (event) => {
		let value = event.target.value;
		if(parseInt(value) > 0) {
			value = event.target.value.replace(/^0+/, '');
		}
		setBaseRate(value);
  }
  
  const [commissionRate, setCommissionRate] = React.useState(0.0);
  
  const handleChangeCommissionRate = (event) => {
		let value = event.target.value;
		if(parseInt(value) > 0) {
			value = event.target.value.replace(/^0+/, '');
		}
		setCommissionRate(value);
  }
  
  const [periodDate, setPeriodDate] = React.useState(new Date());
  
  const handleChangePeriodDate = (newDate) => {
	    console.log('New date is : ' + newDate);
		setPeriodDate(newDate);
  }
  
  const postNewInterestPeriod = () => {
	  
	// Constructing yearMonth
	let month = periodDate.getMonth() + 1;
	month = (month < 10) ? '0' + month.toString() : month.toString();
	let yearMonth = periodDate.getFullYear() + '_' + month;
	console.log('Yearmonth is ' + yearMonth);
	
	let newInterestPeriodOrder = 0;
	if(props.interestPeriod != undefined && props.interestPeriod.length > 0) {
		let maxVal = 0;
		props.interestPeriod.forEach((currentPeriod)=>{if(currentPeriod != undefined && currentPeriod.order > maxVal) {maxVal = currentPeriod.order;}});
		if(maxVal > newInterestPeriodOrder) {newInterestPeriodOrder = maxVal + 1;}
	}
	
	//sendUpdate
	console.log("newInterestPeriodOrder is : " + newInterestPeriodOrder);
	props.sendUpdate('interestPeriod', 'POST', {order : newInterestPeriodOrder, baseRate : baseRate, commissionRate : commissionRate, yearMonth : yearMonth});
	
  }

  return (
    <div>
      <Button style={{borderRadius: "1vh 1vh 1vh 1vh", border: "2px solid rgb(29,155,209)", color: "rgb(29,155,209)", fontWeight: "bold", textTransform: 'none'}}  variant="outlined" onClick={handleClickOpenInterestPeriodCreationDialog}>
        + Dodaj okres oprocentowania
      </Button>
      <Dialog open={openInterestPeriodCreationDialog} onClose={handleCloseInterestPeriodCreationDialog}>
        <DialogTitle>Nowy okres oprocentowania</DialogTitle>
        <DialogContent>
		<div style={{textAlign:"center", paddingTop: "10px"}}>
			<LocalizationProvider locale={pl} adapterLocale={pl} dateAdapter={AdapterDateFns}>
			 <DatePicker
				  views={['year', 'month']}
				  label="Pocztek obowiazywania"
				  minDate={dayjs(new Date())}
				  value={periodDate}
				  okText={"OKEJKA"}
				  clearLabel={React.createElement('span', null, 'Limpar')}
				  cancelLabel={React.createElement('span', null, 'Cancelar')}
				  onChange={handleChangePeriodDate}
				  renderInput={(params) => <TextField {...params} helperText={null} 
				  cancelText={"KANCEL"}
				  />}
				/>
			</LocalizationProvider>
		  <br/><br/>
	      <FormControl sx={{ m: 1 }}>
          <InputLabel htmlFor="outlined-adornment-amount">WIBOR</InputLabel>
          <OutlinedInput type="number"
            id="outlined-adornment-amount"
            value={baseRate}
            onChange={handleChangeBaseRate}
            startAdornment={<InputAdornment position="start">%</InputAdornment>}
            label="WIBOR"
          />
		</FormControl>
	    <FormControl sx={{ m: 1 }}>
          <InputLabel htmlFor="outlined-adornment-amount">Mara</InputLabel>
          <OutlinedInput type="number"
            id="outlined-adornment-amount"
            value={commissionRate}
            onChange={handleChangeCommissionRate}
            startAdornment={<InputAdornment position="start">%</InputAdornment>}
            label="Mara"
          />
        </FormControl>
		</div>
        </DialogContent>
        <DialogActions>
          <Button style={{textTransform: 'none'}} onClick={()=>{handleCloseInterestPeriodCreationDialog(false);}}>Anuluj</Button>
		  <Button style={{textTransform: 'none'}} onClick={()=>{handleCloseInterestPeriodCreationDialog(true);}}>Dodaj</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

function ExcessPaymentCreationDialog(props) {
	
  const [paymentDate, setPaymentDate] = React.useState(new Date());

  const [paymentsEffect, setPaymentsEffect] = React.useState("LOANSHORTENING");
  
  const [commissionRate, setCommissionRate] = React.useState(0.0);
  
  const [paymentAmount, setPaymentAmount] = React.useState(0.0);
  
  const handleSetPaymentAmount = (event) => {
		let value = event.target.value;
		if(parseInt(value) > 0) {
			value = event.target.value.replace(/^0+/, '');
		}
		setPaymentAmount(value);
  }
	
  const [openExcessPaymentCreationDialog, setOpenExcessPaymentCreationDialog] = React.useState(false);

  const handleClickOpenExcessPaymentCreationDialog = () => {
    setOpenExcessPaymentCreationDialog(true);
  };

  const handleCloseExcessPaymentCreationDialog = (propagateChanges) => {
    setOpenExcessPaymentCreationDialog(false);
	if(propagateChanges) {
		// SEND UPDATE
		postNewExcessPayment();
	}
  };
  
  const postNewExcessPayment = () => {
	// Constructing yearMonth
	let month = paymentDate.getMonth() + 1;
	month = (month < 10) ? '0' + month.toString() : month.toString();
	let yearMonth = paymentDate.getFullYear() + '_' + month;
	console.log('Yearmonth is ' + yearMonth);
	
	let newExcessPaymentOrder = 0;
	if(props.excessPayment != undefined && props.excessPayment.length > 0) {
		let maxVal = 0;
		props.excessPayment.forEach((currentPayment)=>{if(currentPayment != undefined && currentPayment.order > maxVal) {maxVal = currentPayment.order;}});
		if(maxVal > newExcessPaymentOrder) {newExcessPaymentOrder = maxVal + 1;}
	}
	
	//sendUpdate
	console.log("newExcessPaymentOrder is : " + newExcessPaymentOrder);
	props.sendUpdate('excessPayment', 'POST', {order: newExcessPaymentOrder, amount : paymentAmount, commission : commissionRate, yearMonth : yearMonth, effect : paymentsEffect});
  
  }

  return (
    <div>
      <Button style={{borderRadius: "1vh 1vh 1vh 1vh", border: "2px solid rgb(29,155,209)", color: "rgb(29,155,209)", fontWeight: "bold", textTransform: 'none'}} variant="outlined" onClick={handleClickOpenExcessPaymentCreationDialog}>
        + Dodaj nadpate
      </Button>
      <Dialog open={openExcessPaymentCreationDialog} onClose={handleCloseExcessPaymentCreationDialog}>
        <DialogTitle>Dodaj nadpate</DialogTitle>
        <DialogContent>
		<div style={{textAlign:"center", paddingTop: "10px"}}>	  
	  <LocalizationProvider sx={{ m: 1 }} locale={pl} dateAdapter={AdapterDateFns}>
		 <DatePicker
			  views={['year', 'month']}
			  label="Data nadpaty"
			  value={paymentDate}
			  onChange={setPaymentDate}
			  onAccept={(newDate)=> {
							console.log('Executing on Accept! ' + newDate);
							if(newDate != undefined) {
								let month = newDate.getMonth() + 1;
								month = (month < 10) ? '0' + month.toString() : month.toString();
								let yearMonth = newDate.getFullYear() + '_' + month;
								console.log('Yearmonth is ' + yearMonth);
								//sendUpdate('excessPayment', 'UPDATE', {key: 'yearMonth', value : yearMonth, order: props.order});
							}
			  }}
			  renderInput={(params) => <TextField {...params} helperText={null} />}
			/>
		</LocalizationProvider>
	  <br/><br/>
	  <FormControl sx={{ m: 1 }}>
          <InputLabel htmlFor="outlined-excess-payment-amount">Kwota</InputLabel>
          <OutlinedInput type="number"
            id="outlined-excess-payment-amount"
            value={paymentAmount}
            onChange={handleSetPaymentAmount}
			onBlur={()=> {}}
            startAdornment={<InputAdornment position="start">PLN</InputAdornment>}
            label="Kwota"
	 />
	 </FormControl> 
		<FormControl sx={{ m: 1, minWidth: 80 }}>
        <InputLabel id="demo-simple-select-autowidth-label">Efekt</InputLabel>
        <Select
          labelId="demo-simple-select-autowidth-label"
          id="demo-simple-select-autowidth"
          value={paymentsEffect}
          onChange={(event)=>setPaymentsEffect(event.target.value)}
          autoWidth
          label="Efekt"
        >
          <MenuItem value={'LOANSHORTENING'}>Skrcenie kredytowania</MenuItem>
          <MenuItem value={'LOWERINSTALLMENT'}>Mniejsze raty</MenuItem>
        </Select>
      </FormControl>
		</div>
        </DialogContent>
        <DialogActions>
          <Button style={{textTransform: 'none'}} onClick={()=>handleCloseExcessPaymentCreationDialog(false)}>Anuluj</Button>
		  <Button style={{textTransform: 'none'}} onClick={()=>handleCloseExcessPaymentCreationDialog(true)}>Dodaj</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

