class DurationInputField extends React.Component {
  constructor(props) {
	super(props);
	this.state = { 
		val : this.props.duration
	};
  }
  
  handleChange = (e) => {
        let value = e.target.value.replace(/\D/g, "");
		value = value.replace(/^0+/, '');
		this.setState({val : value});
  };
	
  render() {
	if(this.props.error === true) {
	
		return (<div style={{backgroundColor : "rgb(255, 255, 255, 0.5)", borderRadius: "1vh 1vh 1vh 1vh"}}>
				<FormLabel id="demo-row-radio-buttons-group-label">Dugo kredytu</FormLabel>
	   			<br/>
				<TextField
				error
				id="outlined-number" type="number" inputProps={{
					step: "1"
				}}
				onBlur={(event)=>{
					let value = undefined;
					value = event.target.value;
					if(value === "") {
						value = 0;
						this.setState({val : value});
					}
					this.props.sendUpdate('duration', 'UPDATE', {value : value});
				}}
				onChange={this.handleChange}
				value={this.state.val}
				InputLabelProps={{shrink: true,}}
				/>
				<br/><br/>
				<div style={{display: "inline-block"}}>
					<Alert severity="error">{this.props.message}</Alert>
				</div>
				<br/><br/>
				</div>);
	} else {
		return (<div>
			<FormLabel id="demo-row-radio-buttons-group-label">Dugo kredytu</FormLabel>
	   		<br/>
			<TextField
			id="outlined-number" type="number" inputProps={{
				step: "1"
			}}
			onBlur={(event)=>{
				let value = undefined;
				value = event.target.value;
				if(value === "") {
					value = 0;
					this.setState({val : value});
				}
				this.props.sendUpdate('duration', 'UPDATE', {value : value});
			}}
			onChange={this.handleChange}
			value={this.state.val}
			InputLabelProps={{shrink: true,}}
			helperText={this.props.message}/>
			</div>);
	}
  }
}

