class ExcessPaymentComponent extends React.Component {
  constructor(props) {
	super(props);
	this.state = { 
		amountState : this.props.amount, 
		commissionState : this.props.commission, 
		yearMonthState : this.props.yearMonth, 
		effectState : this.props.effect
	};
  }
  
  handleChangeAmountState = (e) => {
        let value = e.target.value; //.replace(/\D/g, "");
		if(parseInt(value) > 0) {
			value = value.replace(/^0+/, '');
		}
		this.setState({amountState : value});
  };
	
  render() {
	let localYear = undefined;
	let localMonth = undefined;
	if(this.props != undefined && this.state.yearMonthState != undefined) {
		localYear = this.state.yearMonthState.split('_')[0];
		localMonth = this.state.yearMonthState.split('_')[1];
	}
	
	let generalInfoObject = undefined;
	
	if(this.props.errorDetected) {
		generalInfoObject = {error : this.props.errorDetected, message : this.props.errorMessage};
	}
	
	
    return (<div style={{display: "inline-block", backgroundColor: "rgb(255,255,255)", borderRadius: "1vh 1vh 1vh 1vh", border: "0.2vh solid #d4d5d6", padding: "2vh 2vh 2vh 2vh", marginTop: '15px', marginBottom: '15px', marginLeft: "10px", marginRight: "10px", textTransform: 'none'}}>
			  <ExcessPaymentInfoComponent generalInfoObject={generalInfoObject} />
			 
			  <FormControl sx={{ m: 0, minWidth: 80 }} style={{marginTop: "2vh"}}>
				<InputLabel id="demo-simple-select-autowidth-label">Efekt</InputLabel>
				<Select
				  labelId="demo-simple-select-autowidth-label"
				  id="demo-simple-select-autowidth"
				  value={this.state.effectState}
				  onChange={(event)=>{
					  console.log('NEW PAYMENTS EFFECT IS ' + event.target.value);
					  if(event != undefined) {
						this.setState({effectState : event.target.value});
						this.props.sendUpdate('excessPayment', 'UPDATE', {key: 'effect', value : event.target.value, order: this.props.order});
					  }
				  }}
				  autoWidth
				  label="Efekt"
				>
				  <MenuItem value={'LOANSHORTENING'}>Skrcenie kredytowania</MenuItem>
				  <MenuItem value={'LOWERINSTALLMENT'}>Mniejsze raty</MenuItem>
				</Select>
			  </FormControl>
			  
			  <br/><br/>
			  
			  <FormControl sx={{ m: 0 }}>
				  <InputLabel htmlFor="outlined-excess-payment-amount">Kwota</InputLabel>
				  <OutlinedInput type="number"
					id="outlined-excess-payment-amount"
					value={this.state.amountState}
					onChange={this.handleChangeAmountState}
					onBlur={(event)=> {
									let value = undefined;
									value = event.target.value;
									if(value === "") {
										value = 0;
										this.setState({amountState : value});
									}
									this.props.sendUpdate('excessPayment', 'UPDATE', {key: 'amount', value : value, order: this.props.order});
								
					}}
					startAdornment={<InputAdornment position="start">PLN</InputAdornment>}
					label="Kwota"
			 />
			 </FormControl> 
			 
			 <br/><br/>
			 
			 <LocalizationProvider sx={{ m: 0 }} locale={pl} dateAdapter={AdapterDateFns}>
			 <DatePicker
				  views={['year', 'month']}
				  label="Data nadpaty"
				  value={new Date(parseInt(localYear),parseInt(localMonth)-1,1)}
				  onChange={(newDate)=>{console.log('Executing on change! ' + newDate);
										if(newDate != undefined) {
										let month = newDate.getMonth() + 1;
										month = (month < 10) ? '0' + month.toString() : month.toString();
										let yearMonth = newDate.getFullYear() + '_' + month;
										console.log('On Change Yearmonth is ' + yearMonth);
										this.setState({yearMonthState : yearMonth});
									}
				  }}
				  onAccept={(newDate)=> {
								console.log('Executing on Accept! ' + newDate);
								if(newDate != undefined) {
									let month = newDate.getMonth() + 1;
									month = (month < 10) ? '0' + month.toString() : month.toString();
									let yearMonth = newDate.getFullYear() + '_' + month;
									console.log('Yearmonth is ' + yearMonth);
									this.setState({yearMonthState : yearMonth});
									this.props.sendUpdate('excessPayment', 'UPDATE', {key: 'yearMonth', value : yearMonth, order: this.props.order});
								}
				  }}
				  renderInput={(params) => <TextField {...params} helperText={null} />}
				/>
			</LocalizationProvider>
			<br/><br/>
			<Button spacing={3}  variant="outlined" style={{textTransform: 'none'}}
			onClick={()=>{this.props.sendUpdate('excessPayment', 'DELETE', {value : this.props.order});}}
			>Usu nadpate</Button>
			</div>);
  }
}

class ExcessPaymentInfoComponent extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = { 
		  
	  };
	}

	render() {
		if(this.props.generalInfoObject == undefined) {
			return(
				<div></div>
			);
		} else if(this.props.generalInfoObject.error === true) {
			return(
				<Alert style={{textAlign: "left"}} severity="error">{this.props.generalInfoObject.message}</Alert>
			);
		} else {
			return(
				<Alert style={{textAlign: "left"}} severity="info">{this.props.generalInfoObject.message}</Alert>
			);
		}
	}

}

